@charset "UTF-8";
html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #212121;
  color: #ffffff; }

.main-content {
  margin: 40px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  box-shadow: 0 2px 20px 0 #eeff60, 0 2px 10px 0 red; }

.actions-button {
  display: flex;
  justify-content: flex-end; }

.card-btn {
  background-color: #03a0f1;
  color: #fff; }

body {
  font-family: 'Roboto', sans-serif; }

.record {
  color: #cccccc;
  width: 200px;
  height: 200px; }

.record:hover {
  background-color: #51a351; }

.record-true {
  background-color: #b45859;
  cursor: pointer;
  box-shadow: 0 0 0 rgba(204, 169, 44, 0.4);
  -webkit-animation: pulse 2s infinite;
          animation: pulse 2s infinite; }

@-webkit-keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(204, 169, 44, 0.4); }
  70% {
    box-shadow: 0 0 0 50px rgba(204, 169, 44, 0); }
  100% {
    box-shadow: 0 0 0 0 rgba(204, 169, 44, 0); } }

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(204, 169, 44, 0.4); }
  70% {
    box-shadow: 0 0 0 50px rgba(204, 169, 44, 0); }
  100% {
    box-shadow: 0 0 0 0 rgba(204, 169, 44, 0); } }

.record-false {
  background-color: #03a0f1; }

.rec-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.footer {
  margin: 20px 0;
  position: unset;
  bottom: 0;
  font-size: 20px;
  text-align: center;
  width: 100%;
  font-weight: 500;
  color: #4c4c4c; }

.header {
  display: flex;
  justify-content: space-between;
  background-color: #4c4c4c; }

.card-info {
  margin-left: 10px;
  font-size: 16px; }

.card-title {
  font-size: 20px;
  font-weight: 500; }

.auth-page {
