/*------------------------------------------------------*/
/*Основной контент страницы загрузки снимков*/
.main-content-download {
  flex-grow: 1;
  display: flex;
  margin-left: 60px;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 70px;
  padding-top: 110px; }

.main-content-download .dropzone-container {
  margin-top: 12px;
  margin-left: 34px; }

.main-content-download .dropzone {
  cursor: pointer;
  background-color: rgba(72, 200, 255, 0.03);
  width: 682px;
  height: 662px;
  margin-top: 20px;
  border: 1px dashed #48C8FF;
  border-radius: 5px;
  border-width: 2px; }

.main-content-download .dropzone-container .dropzone .dropzone-content {
  text-align: center;
  position: relative;
  top: 295px; }

.main-content-download .dropzone-container .dropzone .snapshot-list p {
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  color: #48C8FF;
  text-transform: uppercase; }

.main-content-download .dropzone .dropzone-content p {
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  color: #48C8FF;
  text-transform: uppercase; }

.main-content-download .download-info {
  margin-left: 60px;
  width: 465px;
  margin-right: 72px; }

.main-content-download .download-info h2 {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  line-height: normal;
  font-size: 40px; }

.main-content-download .download-info ol {
  padding: 0;
  counter-reset: myCounter; }

.main-content-download .download-info ol li {
  list-style: none;
  position: relative;
  display: flex;
  width: 100%; }

.main-content-download .download-info ol li:before {
  counter-increment: myCounter;
  content: counter(myCounter);
  font-family: Roboto;
  font-size: 24px;
  font-style: normal;
  font-weight: bold;
  color: #FFFFFF;
  background: #0096ef;
  display: inline-flex;
  justify-content: center;
  text-align: center;
  margin: 31px 4px;
  line-height: 49px;
  width: 48px;
  height: 48px;
  border-radius: 24px; }

.main-content-download .download-info ol li:after {
  content: '';
  border-right: 2px dotted #0096ef;
  display: inline-flex;
  margin: 88px 0px -21px -28px; }

.main-content-download .download-info ol li:last-child:after {
  content: '';
  border: none; }

.main-content-download .download-info ol li .step-text {
  display: inline-flex;
  font-family: Roboto;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  font-size: 18px;
  margin-left: 67px;
  position: absolute;
  left: 13px;
  top: 20px;
  min-width: 370px; }

.main-content-download .download-info .submit-file {
  margin-top: 53px;
  height: 68px; }

.main-content-download .download-info .submit-file span {
  font-size: 20px;
  line-height: 2em;
  font-weight: 500; }

/*------------------------------------------------------*/
/*DropZone*/
.dropzone .snapshot-list {
  display: flex;
  width: 305px;
  height: 295px;
  border: 1px solid #48C8FF;
  box-sizing: border-box;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin: 12px;
  flex-flow: column;
  position: relative; }

.dropzone ul {
  display: flex;
  padding-left: 0px;
  margin: 10px;
  justify-content: flex-start;
  flex-wrap: wrap; }

.dropzone ul .snapshot-list .delete-file-icon {
  position: absolute;
  top: 5px;
  right: 10px; }

.dropzone ul .snapshot-list .upload-file-success-message {
  position: absolute;
  top: 260px;
  left: 14px;
  margin-left: 11px;
  vertical-align: top; }

.dropzone ul .snapshot-list .upload-file-success-message span {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 16px;
  color: #48C8FF;
  margin-left: 11px;
  vertical-align: top; }

